import {
	mapState,
	mapMutations
} from 'vuex';
export default {
	data() {
		return {
			sch: 0,
			sx: 0,
			grd: 0,
			sex: ['男', '女'],
			school: [],
			grade: ['大一', '大二', '大三', '大四'],
			imgList: [],
			imgList1: [],
			stuInfo: {
				openid: null,
				name: null,
				sex: null,
				school: null,
				grade: null,
				phone: null,
				stuId: null,
				image: null, //学生证
				shouimage: null //微信头像
			},
		};
	},
	computed: {
		...mapState(['globalOpenid', 'globalWXuserInfo'])
	},
	onLoad() {
		var that = this;
		that.initData();
	},
	methods: {
		...mapMutations(['setGlobalUserInfo', 'setGlobalOpenid', 'setglobalWXuserInfo', 'setToken']),
		// 数据提交
		postInfo() {
			var that = this;
			that.userBindInfo();
		},
		initData() {
			var that = this;
			that.getSchooList();
			// openid 初始化
			that.stuInfo.openid = that.globalOpenid;
			// 微信头像初始化
			that.stuInfo.shouimage = that.globalWXuserInfo.avatarUrl;
			// 表单部分数据初始化
			that.stuInfo.sex = that.sx;

			that.stuInfo.grade = that.grade[that.grd];
			console.log(that.school);
		},
		// 获取学校列表
		async getSchooList() {
			var that = this;
			uni.request({
				url: that.global_baseUrl + '/index.php/school',
				method: "POST",
				success: (res) => {
					if (res.data.code == '200') {
						res.data.data.forEach((item, index) => {
							that.school.push(item.name)
						})

					}
				}
			});
			console.log(that.school);
		},
		// 学生信息表单
		inputMehtod(e) {
			var that = this;
			switch (e.target.dataset.key) {
				case 'name':
					that.stuInfo.name = e.target.value
					break;
				case 'stuId':
					that.stuInfo.stuId = e.target.value
					break;
				case 'phone':
					that.stuInfo.phone = e.target.value
					break;
				default:
					break;
			}
		},
		// 学生绑定信息
		userBindInfo() {
			var that = this;
			that.stuInfo.school = that.school[that.sch];
			var tempData = that.stuInfo;
			if (tempData.image == null) {
				uni.showToast({
					title: '图片未上传！',
					icon: 'none'
				})
			} else {
				uni.showModal({
					title: '提示',
					content: '确定信息正确？',
					success: function(res) {
						if (res.confirm) {
							uni.showLoading({
								title: '提交中'
							})
							uni.request({
								url: that.global_baseUrl + '/index.php/sigin',
								data: tempData,
								method: "POST",
								success: (res) => {
									if (res.data.code == '200') {
										uni.setStorageSync('localUserInfo');
										that.setToken(res.data.data.token);
										uni.setStorageSync('token', res.data.data.token)
										uni.setStorageSync('localUserInfo', res.data.data);
										that.setGlobalUserInfo(res.data.data)
										uni.hideLoading();
										uni.switchTab({
											url: '/pages/home/home'
										})
									} else if (res.data.code == 501) {
										uni.showToast({
											title: res.data.msg,
											icon: 'none'
										})
									} else {

									}
								}
							});
						}
					}
				});
			}

		},
		//学校选择
		schoolChange(e) {
			this.sch = e.detail.value;
			this.stuInfo.school = this.school[this.sch];
		},
		// 性别选择
		sexChange(e) {
			this.sx = e.detail.value;
			this.stuInfo.sex = this.sx;
		},
		// 年级选择
		gradeChange(e) {
			this.grd = e.detail.value
			this.stuInfo.grade = this.grade[this.grd];
		},
		// 图片上传测试
		userUploadFile(picPath, belongUser, picType) {
			var that = this;
			// belongUser 学生0，商户1
			// picType    头像0，学生证1  
			// 				头像0， 门头照1
			var tempData = {
				belongUser: belongUser,
				picType: picType
			}
			uni.uploadFile({
				url: that.global_baseUrl + '/index.php/upPic',
				filePath: picPath,
				name: 'file',
				header: {
					'content-type': 'application/json'
				},
				formData: tempData,
				success: (uploadFileRes) => {
					const tempPath = JSON.parse(uploadFileRes.data).data.src;
					that.stuInfo.image = tempPath.replace(/\\/g, "/");
				}
			});
		},
		ChooseImage() {
			uni.chooseImage({
				count: 1, //默认9
				sizeType: ['original', 'compressed'], //可以指定是原图还是压缩图，默认二者都有
				sourceType: ['album'], //从相册选择
				success: (res) => {
					this.userUploadFile(res.tempFilePaths[0], 0, 1)
					if (this.imgList.length != 0) {

						this.imgList = this.imgList.concat(res.tempFilePaths)
					} else {
						this.imgList = res.tempFilePaths
					}
				}
			});
		},
		ViewImage(e) {
			uni.previewImage({
				urls: this.imgList,
				current: e.currentTarget.dataset.url
			});
		},
		DelImg(e) {
			uni.showModal({
				title: '提示',
				content: '删除该照片',
				cancelText: '手滑',
				confirmText: '确定',
				success: res => {
					if (res.confirm) {
						this.imgList.splice(e.currentTarget.dataset.index, 1)
					}
				}
			})
		},
	}
}
